// app/signup/page.tsx
"use client";
import { useState, useEffect } from "react";
import { useSearchParams } from "next/navigation";
import { loginAs } from "@/lib/auth";

export default function SignupPage() {
  const search_params = useSearchParams();
  const role_from_url = search_params?.get("role") || "student";

  const [form, set_form] = useState({
    id_value: "",
    password: "",
    role: role_from_url,
  });
  const [loading, set_loading] = useState(false);
  const [error_message, set_error_message] = useState("");

  useEffect(() => {
    set_form((prev) => ({ ...prev, role: role_from_url }));
  }, [role_from_url]);

  const get_label = (r: string) => {
    if (r === "admin") return "Admin ID";
    if (r === "lecturer") return "Lecturer ID";
    return "Matric No";
  };

  const get_input_name = (r: string) => {
    if (r === "admin") return "admin_id";
    if (r === "lecturer") return "lecturer_id";
    return "matric_no";
  };

  const handle_change = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) =>
    set_form({ ...form, [e.target.name]: e.target.value });

  const handle_submit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (loading) return;
    if (!form.id_value.trim() || !form.password.trim()) {
      set_error_message("Please fill in all fields.");
      return;
    }

    set_error_message("");
    set_loading(true);

    try {
      // simulate API call
      await new Promise((resolve) => setTimeout(resolve, 1200));
      loginAs(form.role);
      console.log("Signed up as:", form.role, form.id_value);
      // router.push(`/dashboard/${form.role}`)
    } catch (err) {
      console.error(err);
      set_error_message("Something went wrong. Please try again.");
    } finally {
      set_loading(false);
    }
  };

  return (
    <div className="flex items-center justify-center min-h-screen bg-[#F9FAFB] p-4">
      <div className="bg-white p-8 rounded-2xl shadow-lg w-full max-w-md">
        <h1 className="text-2xl font-bold text-[#0B3D2E] mb-2 text-center">
          AFUED {form.role.charAt(0).toUpperCase() + form.role.slice(1)} Signup
        </h1>
        <p className="text-sm text-gray-500 mb-4 text-center">
          Create your account to access the AFUED Portal.
        </p>

        <form onSubmit={handle_submit} className="space-y-4">
          {/* Role selector */}
          <div>
            <label className="block text-sm text-gray-700 mb-1">Select Role</label>
            <select
              name="role"
              value={form.role}
              onChange={handle_change}
              className="w-full border border-[#D1D5DB] rounded-lg p-2 focus:ring-2 focus:ring-[#0B3D2E] outline-none"
            >
              <option value="student">Student</option>
              <option value="lecturer">Lecturer</option>
              <option value="admin">Admin</option>
            </select>
          </div>

          {/* Dynamic ID field */}
          <div>
            <label className="block text-sm text-gray-700 mb-1">{get_label(form.role)}</label>
            <input
              name="id_value"
              type="text"
              value={form.id_value}
              onChange={handle_change}
              placeholder={`Enter your ${get_label(form.role)}`}
              className="w-full border border-[#D1D5DB] rounded-lg p-2 focus:ring-2 focus:ring-[#0B3D2E] outline-none"
              required
            />
          </div>

          {/* Password field */}
          <div>
            <label className="block text-sm text-gray-700 mb-1">Password</label>
            <input
              name="password"
              type="password"
              value={form.password}
              onChange={handle_change}
              placeholder="Enter your password"
              className="w-full border border-[#D1D5DB] rounded-lg p-2 focus:ring-2 focus:ring-[#0B3D2E] outline-none"
              required
            />
          </div>

          {/* Error */}
          {error_message && <p className="text-sm text-red-600">{error_message}</p>}

          {/* Button with loader */}
          <button
            type="submit"
            disabled={loading}
            className={`w-full py-2 rounded-lg text-white font-semibold transition flex items-center justify-center gap-2 ${
              loading ? "bg-[#0B3D2E]/70 cursor-not-allowed" : "bg-[#0B3D2E] hover:bg-[#11543E]"
            }`}
          >
            {loading ? (
              <>
                <svg
                  className="animate-spin h-5 w-5 text-white"
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                >
                  <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                  <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8v4a4 4 0 00-4 4H4z"></path>
                </svg>
                <span>Signing up...</span>
              </>
            ) : (
              <span>Sign Up</span>
            )}
          </button>

          <p className="text-sm text-center text-gray-500 mt-3">
            Already have an account?{" "}
            <a href="/" className="text-[#0B3D2E] font-semibold">
              Login
            </a>
          </p>
        </form>
      </div>
    </div>
  );
}
